<template>
  <div class="ondigo-multistep-form-wrapper">
    <h2 class="mb-4 ondigo-form-header">
      step {{ currentStep }} of {{ lastStep }}
    </h2>
    <v-form
      class="ondigo-multi-step-form ondigo-form"
      :id="formConfig.id"
      @submit.prevent="handleFormSubmit"
      ref="form"
      :name="formConfig.identifier"
      :loading="loading"
    >
	<dynamic-element
		v-for="element in formConfig.elements"
		:key="element.identifier"
		:formName="formConfig.id"
		:element="element"
	/>
	<div v-show="errorCountLabel" class="error-summary my-8">
		<a
			class="text-decoration-underline error--text"
			@click.prevent="scrollToFirstError"
		>{{ errorCountLabel }}</a
		>
	</div>
      <div class="d-flex justify-space-between mt-4">
        <v-btn
          type="button"
          v-if="currentStep > 1"
          @click="loadPreviousStep"
          color="secondary"
          class="ondigo-btn ondigo-btn-back"
        >
          {{ previousButtonLabel }}
        </v-btn>

        <v-btn
          type="submit"
          v-if="isLastStep"
          :loading="loading"
          color="primary"
          class="ondigo-btn ondigo-btn-submit"
        >
          {{ nextButtonLabel }}
        </v-btn>
        <v-btn
          type="submit"
          v-else
          :loading="loading"
          color="primary"
          class="ondigo-btn ondigo-btn-next"
        >
          {{ nextButtonLabel }}
        </v-btn>
      </div>
    </v-form>
  </div>
</template>

<script>
export default {
  computed: {
    formConfig() {
      return this.$store.getters.getCurrentSchema;
    },
    loading() {
      return this.$store.state.loading;
    },
	  errorCountLabel(){
		  return this.$store.getters.getErrorLabel;
	  },
    currentStep() {
      return this.$store.state.currentStep;
    },

    lastStep() {
      return this.$store.state.lastStep;
    },

    isLastStep() {
      return this.currentStep === this.lastStep;
    },
    buttonLabels() {
      return (
        this.formConfig &&
        this.formConfig.api &&
        this.formConfig.api.page &&
        this.formConfig.api.page.labels
      );
    },
    previousButtonLabel() {
      return (
        (this.buttonLabels && this.buttonLabels.previousButtonLabel) ||
        "previous step"
      );
    },

    nextButtonLabel() {
      return (
        (this.buttonLabels && this.buttonLabels.nextButtonLabel) || "next step"
      );
    },
  },
  inject: ["scrollToErrorCallback"],
  methods: {
    handleFormSubmit() {
      const form = this.$refs.form;
      if (form) this.$store.dispatch("submitStep", form);
    },
    loadPreviousStep() {
      this.$store.commit("updateFormStep", this.currentStep - 1);
    },
    scrollToFirstError(scrollToErrorCallback) {
      const firstInputWithError = this.$refs.form.$el.querySelector(
        ".v-input.error--text"
      );
      if (firstInputWithError) {
        if (scrollToErrorCallback) {
          scrollToErrorCallback(firstInputWithError);
        }
      }
    },
  },
};
</script>
